*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;

}

.container-formulario{
  display: flex;
  padding: 2em;
  width: 100%;
  max-width: 400px;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container-meme{
  max-width: 400px;
  
  background-color: black;
}

input, select{
  width: 100%;
  margin-top: 5px;
}

.container-meme p{
  position: relative;
  text-transform: uppercase;
  font-size: 1.3em;
  color: whitesmoke;
  font-weight: bold;
}


img{
  width: 100%;
  max-width: 400px;
  
}

.container-footer {
  position: fixed;
  background-color: #2d2d2d;
  color: whitesmoke;
  bottom: 0px;
  width: 100%;
}